:root {

	/* COLORS (Keep in sync with inc/customizer.php) */
	--color-text: #00101c;
	--color-text-light: #6b6b6b;

	--color-link: #{$p2020-blue};
	--color-link-dark: #{darken($p2020-blue, 13%)};
	--color-link-background: #dfeafb;

	// Border colors are not yet reconciled with updated P2 brand colors
	--color-border: #dbdde1;
	--color-border-light: #e9eaed;
	--color-border-dark: #75808a;

	--color-background-very-light: #f9f9f9;
	--color-background-light: #f3f3f3;
	--color-background-dark: #00101c;

	--color-mentions: #{$p2020-purple};
	--color-mentions-highlight: #{rgba($p2020-purple, 0.1)};

	--color-white: #{$p2020-white};
	--color-black: #{$p2020-black};
	--color-green: #1cca7b; // not yet reconciled with updated P2 brand colors
	--color-green-dark: rgb(18, 160, 96);
	--color-green-light: #d4f8d4;

	/* TYPOGRAPHY */
	--font-default: #{$font-sans};
	--font-body: #{$font-sans};
	--font-mono: #{$font-mono};

	/* ANIMATION */
	--transition-quick: 0.1s;
}
