/**
 * Styles for block editor (Gutenberg) content
 * https://developer.wordpress.org/block-editor/developers/themes/theme-support/#editor-styles
 *
 * Note: This stylesheet is consumed by the normal block editor (wp-admin/calypso),
 * but does not affect the block editor in p2tenberg.
 */

/**
 * Variables & Utilities
 */
@import "css/src/global/functions";
@import "css/src/global/variables";
@import "css/src/global/themes";
@import "css/src/global/mixins";

/**
 * Core
 */
@import "css/src/core/wp-caption";

// All blocks
@import "css/src/core/blocks/_wp-block-code.scss";
@import "css/src/core/blocks/_wp-block-image.scss";
@import "css/src/core/blocks/_wp-block-pullquote.scss";
@import "css/src/core/blocks/_wp-block-quote.scss";
@import "css/src/core/blocks/_wp-block-verse.scss";

/**
 * UI
 */
@import "css/src/ui/p2020-block-editor-content";
@import "css/src/ui/p2020-caption";
@import "css/src/ui/p2020-entry-content";
@import "css/src/ui/p2020-table";
@import "css/src/dependencies/jetpack/inline-terms";

body {
	@extend %p2020-block-editor-content;
}
